<template>
    <div>
        <nav-bar>
            <template v-slot:default>个人中心</template>
        </nav-bar>

        <div class="user-box">
            <div class="user-info">
                <div class="info">
                    <img src="https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fbpic.588ku.com%2Felement_origin_min_pic%2F17%2F04%2F10%2F8f0d1ecca24308cc22591a44987dd05c.jpg%21%2Ffwfh%2F804x705%2Fquality%2F90%2Funsharp%2Ftrue%2Fcompress%2Ftrue&refer=http%3A%2F%2Fbpic.588ku.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=jpeg?sec=1616743832&t=7e9c2c97ad90230e1c5abbfda2e7ef60" alt="" />
                    <div class="user-desc">
                        <span>昵称：{{user.name}}</span>
                        <span>登录名：{{user.email}}</span>
                    </div>
                </div>
            </div>

            <ul class="user-list">
                <li class="van-hairline--bottom" @click="goTo('/collect')">
                    <span>我的收藏</span>
                    <van-icon name="arrow" />
                </li>
                <li class="van-hairline--bottom" @click="goTo('/order')">
                    <span>我的订单</span>
                    <van-icon name="arrow" />
                </li>
                <li class="van-hairline--bottom" @click="goTo('/setting')">
                    <span>账号管理</span>
                    <van-icon name="arrow" />
                </li>
                <li class="van-hairline--bottom" @click="goTo('/addresslist')">
                    <span>地址管理</span>
                    <van-icon name="arrow" />
                </li>
                <li class="van-hairline--bottom" @click="goTo('/about')">
                    <span>关于我们</span>
                    <van-icon name="arrow" />
                </li>
            </ul>
        </div>

        <div style="margin:12px;margin-top:50px">
            <van-button @click="tologout" round block color="#ff5000">退出登录</van-button>
        </div>
    </div>
</template>

<script>
import NavBar from '../../components/common/navbar/NavBar.vue';
import {ref, reactive, toRefs, onMounted} from 'vue';
import {logout, getUser} from '../../network/user';
import { Toast } from 'vant';
import {useRouter} from 'vue-router';
import {useStore} from 'vuex';

export default {
    name:'Profile',
    components: {
        NavBar
    },
    setup() {
        const router = useRouter();
        const store = useStore();

        const state = reactive({
            user:{}
        })

        onMounted(() => {
            getUser().then(res=> {
                // console.log(res);
                state.user = res;
            })
        })

        const tologout = () => {
            logout().then(res => {
                if(res.status == '204') {
                    Toast.success('退出成功');

                    // 清除token window.localStroage
                    window.localStorage.setItem('token', '');

                    store.commit('setIsLogin', false);

                    setTimeout(() => {
                        router.push({path:'/login'});
                    }, 1300);
                }
            })
        }

        // 跳转方法
        const goTo = (path, query) => {
            router.push({path, query:query || {}});
        }
        return {
            tologout,
            ...toRefs(state),
            goTo
            
        }
    }
}
</script>

<style lang="scss" scoped>
    // #box {
    //     // background: ;
    //     // height: 100vh;
    // }
    .user-box {
        margin-top: 65px;


    }
    .user-info {
        width: 94%;
        margin: 10px;
        height: 115px;
        background: linear-gradient(90deg, );
        background: rgb(253, 170, 111);
        box-shadow: 0 2px 5px #ff9000;
        border-radius: 8px;
        margin-top: 50px;
        text-align: left;

        .info {
            position: relative;
            display: flex;
            width: 100%;
            height: 100%;
            padding: 25px 20px;

            img {
                width: 90px;
                height: 75px;
                border-radius: 50%;
                margin-right: 12px;
            }
            .user-desc {
                display: flex;
                flex-direction: column;
                line-height: 22px;
                font-size: 14px;
                color:  #333;
                span {
                    color: #333;
                    font-size: 14px;
                    padding: 2px 0;
                }
            }

        }
    }
    .user-list {
        padding: 0 8px;
        margin-top: 40px;
        li {
            padding: 5px;
            height: 38px;
            line-height: 32px;
            display: flex;
            justify-content: space-between;
            font-size: 16px;
            background: #ffffff38;
            margin: 20px 0;
            // box-shadow: 0 0px 1px #ff5000;
            border-radius: 3px;
            .van-icon-arrow {
                margin-top: 8px;
            }

        }

    }
</style>